.projectImage {
    opacity: 1;
  display: block;
  width: 100%;
  height: auto;
  -webkit-transition: .5s ease;
  -o-transition: .5s ease;
  transition: .5s ease;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  border-radius: 2%
}

#portfolioStart {
  height: auto;
  overflow: auto;
}

.directions {
    overflow:auto;
}

.proj-container {
    position: relative;
    width: 100%;
    margin-bottom: 3vh;
    margin-left: auto;
  margin-right: auto;
  -ms-flex-pack: center;
      justify-content: center;
 
}

.middle {
  -webkit-transition: .5s ease;
  -o-transition: .5s ease;
  transition: .5s ease;
  opacity: 0;
  background-color: ivory;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
}

.proj-container:hover .projectImage {
  opacity: 0.3;
}

.proj-container:hover .middle {
  opacity: 1;
}

.text {
  background-color: #51B8DB;
  color: ivory;
  font-size: 26px;
  padding: 16px 32px;
}

.descrip {
    font-size: 18px;
}

.text:hover {
    cursor: pointer;
}

.modal-content {
  color: #050919 !important;
}

.portModal {
  margin-top: 5vh;
}


